import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';

@Component({
  selector: 'app-list-backgroundmusic',
  templateUrl: './list-backgroundmusic.component.html',
  styleUrls: ['./list-backgroundmusic.component.css']
})
export class ListBackgroundmusicComponent implements OnInit {
  bgmArr: any
  constructor(private adminService: AdminService) { }

  ngOnInit() {
    this.listBackgroundMusicInfo()
  }

  listBackgroundMusicInfo() {
    this.adminService.listBackgroundMusic().subscribe(res => {
      console.log('response', res)
      if (res.code == 200) {
        this.bgmArr = res.data
      }
    })
  }

  delete(id) {
    console.log("The deleting id is " + id  ) ;
    this.adminService.deleteBackgroundMusic(id).subscribe(res=>{
      console.log('response', res);
      if (res.code == 200) {
        this.listBackgroundMusicInfo()
      }
    },
    error => {
      return console.log('There was an error: ', error);
    }); 
  }
}
