import { Component, OnInit } from '@angular/core';
import { Validators, FormGroup, FormControl } from '@angular/forms';
import { AdminService } from 'src/app/admin/admin.service';

@Component({
  selector: 'app-add-backgroundmusic',
  templateUrl: './add-backgroundmusic.component.html',
  styleUrls: ['./add-backgroundmusic.component.css']
})
export class AddBackgroundmusicComponent implements OnInit {
  addBackgroundMusicForm: FormGroup
  constructor(private adminService: AdminService) { }


  ngOnInit() {
    this.addBackgroundMusicForm = new FormGroup({
      music_url: new FormControl('', [
        Validators.required,
      ]),
      musicName: new FormControl('', [
        Validators.required,
      ])
     
    });
  }

  onAddMusic() {
    if (this.addBackgroundMusicForm.valid) {
      this.adminService.addBackgroundMusic(this.addBackgroundMusicForm.value).subscribe(res => {
        console.log('response', res)
      })
    } else {

    }
  }

}
