import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddBackgroundmusicComponent } from './add-backgroundmusic/add-backgroundmusic.component';
import { EditBackgroundmusicComponent } from './edit-backgroundmusic/edit-backgroundmusic.component';
import { ListBackgroundmusicComponent } from './list-backgroundmusic/list-backgroundmusic.component';

const routes: Routes = [
  {
    path: 'add-background',
    component: AddBackgroundmusicComponent
  },
  {
    path: 'list-background',
    component: ListBackgroundmusicComponent
  },
  {
    path: 'edit-background/:id',
    component: EditBackgroundmusicComponent
  } 
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManageBackgroundmusicRoutingModule { }
